import { FC, useState } from "react";
import styles from './adding-new-product.module.scss';

import { useAppDispatch, useAppSelector } from "../../../hooks";
import {
	addNewProduct,
	manageSybtypeForNewProduct,
	manageTypeForNewProduct,
	resetSubtypeForNewProduct,
	resetTypeForNewProduct
} from "../../../store/goodSlice";
import { IProduct } from "../../../types/IProduct";

export interface ICheckboxProps {
	name: string,
	value: string,
	isChecked: boolean
}

const AddingNewProduct: FC = () => {
	const dispatch = useAppDispatch()

	const { managingTypes, managingSubtypes } = useAppSelector(state => state.goodReducer);

	const [title, setTitle] = useState('');
	const [imgUrl, setImgUrl] = useState('');
	const [typeSize, setTypeSize] = useState('');
	const [size, setSize] = useState('');
	const [barcode, setBarcode] = useState('');
	const [manufacturer, setManufacturer] = useState('');
	const [brand, setBrand] = useState('');
	const [description, setDescription] = useState('');
	const [price, setPrice] = useState('');

	const defaultImg = 'data:image/jpeg;base64,';
	const getId = () => Math.floor(Math.random() * 1000).toString();

	const submitHandler = (e: React.SyntheticEvent) => {
		e.preventDefault();

		const checkedType = managingTypes
			.filter((type) => type.isChecked === true)
			.map((type) => type.name);

		const checkedSubtype = managingSubtypes
			.filter((subtype) => subtype.isChecked === true)
			.map((subtype) => subtype.name);

		const newProduct: IProduct = {
			id: getId(),
			imgUrl: imgUrl || defaultImg,
			title,
			types: checkedType,
			subtypes: checkedSubtype,
			typeSize,
			size,
			barcode,
			manufacturer,
			brand,
			description,
			price
		};

		dispatch(addNewProduct(newProduct));

		const storage = localStorage.getItem('addedGoods');
		let parse: IProduct[];

		if (typeof storage === 'string') {
			// eslint-disable-next-line @typescript-eslint/no-unsafe-assignment
			parse = JSON.parse(storage);
			const updatedStorage = [...parse, newProduct];
			localStorage.setItem('addedGoods', JSON.stringify(updatedStorage));
		}

		dispatch(resetTypeForNewProduct());
		dispatch(resetSubtypeForNewProduct());
		setTitle('');
		setImgUrl('');
		setTypeSize('');
		setSize('');
		setBarcode('');
		setManufacturer('');
		setBrand('');
		setDescription('');
		setPrice('');
	}



	const Checkbox: FC<ICheckboxProps> = ({ name, value, isChecked }) => {
		const [checked, toggleChecked] = useState(isChecked);
		const dispatch = useAppDispatch();


		const clickHandler = () => {
			toggleChecked(!checked);

			if (name === 'subtype') {
				const changingSubtype = { name: value, isChecked: !checked };
				dispatch(manageSybtypeForNewProduct(changingSubtype));

			} else if (name === 'type') {
				const changindType = { name: value, isChecked: !checked };
				dispatch(manageTypeForNewProduct(changindType));
			}
		}

		return (
			<div className={styles.checkbox_container} onClick={clickHandler} >

				<input readOnly className={styles.checkbox} name={value} type="checkbox" checked={checked} />
				<label htmlFor={value} className={styles.label}>{value}</label>

			</div>
		);
	}


	return (
		<div className={styles.container}>
			<h2 className={styles.title}>  </h2>
			<form name="form" className={styles.form} onSubmit={submitHandler}>

				<div className={styles.input_container}>
					<label className={styles.label} htmlFor="title">  </label>
					<input required className={styles.input} value={title} onChange={(e) => setTitle(e.target.value)} name="title" type="text" />
				</div>

				<div className={styles.input_container}>
					<label className={styles.label} htmlFor="imgUrl"> URL  </label>
					<input className={styles.input} value={imgUrl} onChange={(e) => setImgUrl(e.target.value)} name="imgUrl" type="text" />
				</div>

				<div className={styles.checkbox_container}>
					<span className={styles.checkbox_title}>{' : '}</span>
					{managingTypes.map(({ name, isChecked }) => {
						return (
							<Checkbox name={'type'} key={name} value={name} isChecked={isChecked} />
						)
					})}
				</div>

				<div className={styles.checkbox_container}>
					<span className={styles.checkbox_title}>{' : '}</span>
					{managingSubtypes.map(({ name, isChecked }) => {
						return (
							<Checkbox name={'subtype'} key={name} value={name} isChecked={isChecked} />
						)
					})}
				</div>

				<div className={styles.input_container}>
					<label className={styles.label} htmlFor="typeSize">    </label>
					<input required className={styles.input} value={typeSize} onChange={(e) => setTypeSize(e.target.value)} name="typeSize" type="text" />
				</div>

				<div className={styles.input_container}>
					<label className={styles.label} htmlFor="size"> / </label>
					<input required className={styles.input} value={size} onChange={(e) => setSize(e.target.value)} name="size" type="text" />
				</div>

				<div className={styles.input_container}>
					<label className={styles.label} htmlFor="brand">  </label>
					<input required className={styles.input} value={brand} onChange={(e) => setBrand(e.target.value)} name="brand" type="text" />
				</div>

				<div className={styles.input_container}>
					<label className={styles.label} htmlFor="manufacturer">  </label>
					<input required className={styles.input} value={manufacturer} onChange={(e) => setManufacturer(e.target.value)} name="manufacturer" type="text" />
				</div>

				<div className={styles.input_container}>
					<label className={styles.label} htmlFor="barcode">  </label>
					<input required className={styles.input} value={barcode} onChange={(e) => setBarcode(e.target.value)} name="barcode" type="text" />
				</div>

				<div className={styles.input_container}>
					<label className={styles.label} htmlFor="description">  </label>
					<textarea required className={styles.textarea} value={description} onChange={(e) => setDescription(e.target.value)} name="description" />
				</div>

				<div className={styles.input_container}>
					<label className={styles.label} htmlFor="price">    </label>
					<input required className={styles.input} value={price} onChange={(e) => setPrice(e.target.value.replace(/[^0-9]/g, ""))} name="price" type="text" />
				</div>

				<button className={styles.submit} type="submit"></button>
			</form>
		</div>
	)
}

export default AddingNewProduct;